import React from "react";
import { useParams } from "react-router-dom";
import { useMutation } from "react-query";
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import { resetPassword } from "../API-utils/endpointsAuthUser";

const LoginSchemat = Yup.object().shape({
  password: Yup.string()
    .required("Haso jest wymagane!")
    .min(8, "Haso musi zawiera minimum 8 znakw.")
    .matches(/^.*(?=.*\d).*$/, "Haso musi zawiera przynajmniej jedn cyfr.")
    .matches(
      /^.*((?=.*[a-z,A-Z])).*$/,
      "Haso musi zawiera przynajmniej jedn liter."
    ),

  passwordConfirmation: Yup.string()
    .required("Pole jest wymagane!")
    .oneOf([Yup.ref("password")], "Podane hasa nie s identyczne"),
});

const ResetPassword = () => {
  const { token, email } = useParams();

  let content;

  const { isSuccess, mutate } = useMutation(resetPassword, {});

  if (isSuccess) {
    content = (
      <section className="popUpPassword">
        <div className="popUpPassword__200">
          <p>Zmiana hasa zostaa pomylnie dokonana</p>
        </div>
      </section>
    );
  }

  return (
    <div className="sign-wrapper sign-wrapper--wider">
      <p className="sign-change-password">
        Ustal nowe haso dla Twojego konta.
      </p>
      {content}
      <Formik
        initialValues={{
          token,
          email,
          password: "",
          passwordConfirmation: "",
        }}
        validationSchema={LoginSchemat}
        onSubmit={(values) => {
          mutate(values);
        }}
      >
        {({ handleSubmit }) => (
          <Form onSubmit={handleSubmit} className="sign-form">
            <Field
              id="password"
              name="password"
              placeholder="Haso"
              type="password"
              className="sign-form__input"
            />

            <div className="errors">
              <ErrorMessage name="password" />
            </div>

            <div className="password-info">
              <p className="password-info__text">
                Pamitaj by haso zawierao:
              </p>
              <ul className="password-info__list">
                <li className="password-info__list-item">minimum 8 znakw</li>
                <li className="password-info__list-item">
                  przynajmniej jedn liter
                </li>
                <li className="password-info__list-item">
                  przynajmniej jedn cyfr
                </li>
              </ul>
            </div>

            <Field
              id="passwordConfirmation"
              name="passwordConfirmation"
              placeholder="Powtrz haso"
              type="password"
              className="sign-form__input"
            />

            <div className="errors">
              <ErrorMessage name="passwordConfirmation" />
            </div>

            <button type="submit" className="sign-form__button">
              Zmie haso
            </button>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default ResetPassword;
