import React from "react";
import { NavLink } from "react-router-dom";
import { useQuery, useMutation } from "react-query";
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import { userAuth, registerAuth } from "../API-utils/endpointsAuthUser";

const LoginSchemat = Yup.object().shape({
  username: Yup.string()
    .required("Nazwa uytkownika jest wymagana!")
    .min(5, "Nazwa uytkownika musi skada si z conajmniej 5 znakw.")
    .max(20, "Nazwa uytkownika moe skada si maksymalnie z 20 znakw"),

  email: Yup.string()
    .email("Niepoprawny e-mail!")
    .required("E-mail jest wymagany!"),

  password: Yup.string()
    .required("Haso jest wymagane!")
    .min(8, "Haso musi zawiera minimum 8 znakw.")
    .matches(/^.*(?=.*\d).*$/, "Haso musi zawiera przynajmniej jedn cyfr.")
    .matches(
      /^.*((?=.*[a-z,A-Z])).*$/,
      "Haso musi zawiera przynajmniej jedn liter."
    ),

  passwordConfirmation: Yup.string()
    .required("Pole jest wymagane!")
    .oneOf([Yup.ref("password")], "Podane hasa nie s identyczne"),
});

const Register = () => {
  let content;
  let infoRegister;

  const { isSuccess, isError, mutate } = useMutation(registerAuth, {});

  const { status: isLogged, data } = useQuery("user", userAuth, { retry: 0 });

  if (isSuccess) {
    infoRegister = (
      <section className="popUp">
        <div class="popUp__register201">
          <p>Twoje konto zostao pomylnie utworzone.</p>
          <p>Moesz si teraz zalogowa.</p>
          <NavLink to="/login">
            <button className="popUp__button">Zaloguj si</button>
          </NavLink>
        </div>
      </section>
    );
  }
  if (isError) {
    infoRegister = (
      <p className="registerError">Konto z tymi danymi ju istnieje</p>
    );
  }

  if (isLogged === "loading") {
    content = (
      <div className="spinner__box spinner__box--center">
        <div className="spinner__load"></div>
      </div>
    );
  } else {
    if (isLogged === "success") {
      content = (
        <section className="userLogged__box">
          <p className="userLogged__text">
            Brak dostpu do tej funkcjonalnoci
          </p>
          <p className="userLogged__text">
            z poziomu zalogowanego uytkownika.
          </p>
        </section>
      );
    } else {
      content = (
        <div className="sign-wrapper">
          <section className="sign-choose">
            <NavLink
              className={({ isActive }) =>
                isActive
                  ? "sign-choose__type login_selected"
                  : "sign-choose__type"
              }
              to="/login"
            >
              Zaloguj si
            </NavLink>
            <NavLink
              className={({ isActive }) =>
                isActive
                  ? "sign-choose__type register_selected"
                  : "sign-choose__type"
              }
              to="/register"
            >
              Za konto
            </NavLink>
          </section>
          {infoRegister}
          <Formik
            initialValues={{
              username: "",
              email: "",
              password: "",
              passwordConfirmation: "",
            }}
            validationSchema={LoginSchemat}
            onSubmit={(values) => {
              mutate(values);
            }}
          >
            {({ handleSubmit }) => (
              <Form onSubmit={handleSubmit} className="sign-form">
                <Field
                  id="username"
                  name="username"
                  placeholder="Nazwa uytkownika"
                  type="text"
                  className="sign-form__input"
                />
                <div className="errors">
                  <ErrorMessage name="username" />
                </div>

                <Field
                  id="email"
                  name="email"
                  placeholder="E-mail"
                  type="email"
                  className="sign-form__input"
                />
                <div className="errors">
                  <ErrorMessage name="email" />
                </div>

                <Field
                  id="password"
                  name="password"
                  placeholder="Haso"
                  type="password"
                  className="sign-form__input"
                />
                <div className="errors">
                  <ErrorMessage name="password" />
                </div>

                <div className="password-info">
                  <p className="password-info__text">
                    Pamitaj by haso zawierao:
                  </p>
                  <ul className="password-info__list">
                    <li className="password-info__list-item">
                      minimum 8 znakw
                    </li>
                    <li className="password-info__list-item">
                      przynajmniej jedn liter
                    </li>
                    <li className="password-info__list-item">
                      przynajmniej jedn cyfr
                    </li>
                  </ul>
                </div>

                <Field
                  id="passwordConfirmation"
                  name="passwordConfirmation"
                  placeholder="Powtrz haso"
                  type="password"
                  className="sign-form__input"
                />
                <div className="errors">
                  <ErrorMessage name="passwordConfirmation" />
                </div>

                <button type="submit" className="sign-form__button">
                  Za konto
                </button>
              </Form>
            )}
          </Formik>
        </div>
      );
    }
  }

  return <>{content}</>;
};

export default Register;
